import React, { Component } from 'react'
import { Form, Input, Icon } from 'antd';

const { Item } = Form; 

export default class EventForm extends Component {
  state = {
    category: null,
    event: '',
    date: '',
    startTime: null,
    endTime: null,
  }

  render() {
    return (
      <Form>
        <Item>
          <Input
            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
            placeholder="Username"
          />
        </Item>
      </Form>
    )
  }
}
